html {
  box-sizing: border-box;
  height: 100%; }

*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  font-size: 16px;
  font-family: Georgia, serif;
  line-height: 1.5;
  color: #2c3e50;
  position: relative;
  padding-bottom: 6rem;
  margin: 0; }
  @media (only-screen) and (min-width: 37.5rem) and (max-width: 2097151.75rem) {
    body {
      font-size: 110%;
      line-height: 1.375; } }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  font-family: "Source Sans Pro", sans-serif;
  margin-top: 0.0625rem;
  margin-bottom: .3em;
  text-transform: uppercase; }
  h1 + p,
  h1 + ul,
  h1 + ol,
  h2 + p,
  h2 + ul,
  h2 + ol,
  h3 + p,
  h3 + ul,
  h3 + ol,
  h4 + p,
  h4 + ul,
  h4 + ol,
  h5 + p,
  h5 + ul,
  h5 + ol,
  h6 + p,
  h6 + ul,
  h6 + ol {
    margin-top: 0; }

h1 {
  font-size: 2rem; }
  @media (only-screen) and (min-width: 37.5rem) and (max-width: 2097151.75rem) {
    h1 {
      font-size: 2.375rem; } }

h2 {
  font-size: 1.5rem; }
  @media (only-screen) and (min-width: 37.5rem) and (max-width: 2097151.75rem) {
    h2 {
      font-size: 1.8rem; } }

h3 {
  font-size: 1.375rem; }
  @media (only-screen) and (min-width: 37.5rem) and (max-width: 2097151.75rem) {
    h3 {
      font-size: 1.5rem; } }

h4 {
  font-size: 1.25rem; }

p + h2,
p + h3,
p + h4,
p + h5,
p + h6,
ul + h2,
ul + h3,
ul + h4,
ul + h5,
ul + h6,
ol + h2,
ol + h3,
ol + h4,
ol + h5,
ol + h6 {
  margin-top: 0; }

ul,
ol {
  padding-left: 1.875em; }
  ul li,
  ol li {
    line-height: 1.25;
    margin-bottom: 0.46875em; }

img {
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic; }

a {
  transition-duration: .2s;
  text-decoration: none;
  color: #2c3e50;
  outline: 0; }

aside {
  width: 100%;
  float: left;
  position: relative; }

section {
  width: 100%;
  float: left;
  position: relative; }

.main {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 62.5rem; }

.main-inner {
  width: 100%;
  float: left;
  position: relative;
  padding: 0.9375em; }

.to-top {
  display: block;
  position: fixed;
  right: 1.875em;
  bottom: 1.875em;
  max-height: 2.5rem;
  max-width: 2.5rem;
  height: 25vw;
  width: 25vw; }
  .to-top a {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle; }
    .to-top a::before {
      content: "";
      font-family: FontAwesome;
      vertical-align: inherit;
      font-size: 2rem;
      line-height: 40px; }

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  background-color: #efefef;
  text-align: center; }
